.container{
    margin: 0;
    padding: 0;
    font-family:Verdana, Geneva, Tahoma, sans-serif;
}

.headline{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: darkkhaki;
    padding: 5px;
    width: 100%;
    margin-left: 0;
   
}
.headimg{
    border-bottom: 2px solid black;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-bottom: 10px;
}
img{
    width: 4rem;
    height: 4rem;
    
}
