.card{
  height: 140px;
  width: 290px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.col-md-3{

  display: flex;
  align-items: center;
  justify-content: center;
  
  padding: 6.3rem 0;
}
div.maindiv{
  
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin: 0 !important;
}


.card:hover {
  transform: translatex(5px);
}


@media only screen and (max-width: 768px) {
  .col-md-3{  
    padding: 10px 0;
  }
  
  
}